@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
.Carousel__list{display:flex;padding-left:0;margin-bottom:30px;overflow:hidden;list-style:none;border:2px solid #21765e}.Carousel__img{padding:10px 0}.Carousel__controls{display:flex;justify-content:center;grid-gap:35px;gap:35px}.Carousel__btn{cursor:pointer;font-size:20px;width:100px;height:45px;color:#fff;border:2px solid #21765e;background-color:transparent}.Carousel__btn__btn--active:hover{color:#00ffb7;border-color:#00ffb7}.Carousel__btn__btn--disabled{color:#364843;border-color:#364843;cursor:no-drop}

html,#root{color:#fff;font-family:Montserrat, sans-serif;background-color:#09142c}::-webkit-scrollbar-track{background-color:#c0c1c2}::-webkit-scrollbar{width:5px;background-color:#c0c1c2}::-webkit-scrollbar-thumb{background-color:#808080}.title{margin:30px 0 40px;font-weight:400;font-size:26px}.App{display:flex;flex-direction:column;align-items:center}.App__wrapper{max-width:740px;margin-bottom:20px;display:flex;justify-content:space-between;flex-wrap:wrap}.App__label{display:flex;justify-content:space-between;width:230px;height:40px;margin:15px auto;padding:2px 15px;font-size:18px;align-items:center;border-left:3px solid #21765e;box-shadow:0 2px 6px rgba(0,0,0,0.1);cursor:pointer}.App__label:hover{border-left:3px solid #00ffb7;box-shadow:10px 10px 10px rgba(0,0,0,0.2)}.App__input{width:50px;height:25px;margin:auto 0;border:1px solid #21765e;border-radius:50px;font-weight:700;font-size:12px;color:#fff;background-color:#09142c;text-align:center;outline:none;cursor:pointer}.App__input:hover{border:1px solid #00ffb7}.App input[role="switch"]{--switch-handle-size: 8px;--switch-bg-size: 24px;--switch-handle-gap: 3px;--switch-fg: #00ffb7;--switch-bg: color-mix(in sRGB, var(--switch-fg) 20%, transparent);appearance:none;-webkit-appearance:none;box-sizing:content-box;display:inline-flex;padding-inline:0 var(--switch-bg-size);border:1px solid #21765e;font-size:0;margin:0;height:auto;width:auto;border-radius:100px;background:var(--switch-bg);transition:0.2s cubic-bezier(0.65, 0, 0.5, 1);transition:var(--switch-duration, 0.2s) cubic-bezier(0.65, 0, 0.5, 1);-webkit-user-select:none;-ms-user-select:none;user-select:none;outline:none;-webkit-filter:grayscale(1);filter:grayscale(1);cursor:pointer}.App input[role="switch"]:hover{border:1px solid #00ffb7}.App input[role="switch"]:checked{padding-inline:var(--switch-bg-size) 0;-webkit-filter:none;filter:none}.App input[role="switch"]::before{content:"";padding:var(--switch-handle-size);margin:var(--switch-handle-gap);background:var(--switch-fg);border-radius:50%;transition:inherit;border:1px solid #21765e}

